@import "./shared/animation/anim.scss";

.App {
  background-color: rgb(19, 19, 19);
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100vh;
  overflow: hidden;
  align-items: center;
  justify-content: center;

  .bg-image {
    position: absolute;
    height: 100vh;
    background-image: url("./assets/gifs/bg.gif");
    background-size: cover;

    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    left: 0;
    right: 0;
  }
  .bg-opacity {
    position: absolute;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    left: 0;
    right: 0;
  }

  h1 {
    font-size: 3rem;
    color: white;

    font-family: CascadiaCode;
    letter-spacing: 8px;
    // margin-left: 25px;

    z-index: 1;
  }

  .description {
    color: white;
    font-family: CascadiaCode;
    letter-spacing: 3px;
    font-size: 1.2rem;

    padding: 16px;

    z-index: 1;

    .name-link {
      text-decoration: underline;
    }

    .name-link:hover {
      color: orange;
      background-color: rgba(255, 166, 0, 0.11);
      transition: 250ms;
      cursor: pointer;
    }

    .dot {
      opacity: 0;
      -webkit-animation: dot 2s infinite;
      animation: dot 2s infinite;

      $chars: 4;
      @for $i from 1 through $chars {
        &:nth-of-type(#{$i}) {
          animation-delay: 1200ms+180ms * $i;
        }
      }
    }
  }

  .music-player {
    z-index: 1;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 8px 32px 8px 16px;

    display: flex;
    flex-direction: column;
    align-items: flex-end;

    h1 {
      font-size: 0.8rem;
    }
  }
}

.bounce {
  animation: bounce ease-in-out 2s infinite alternate-reverse;
  opacity: 1;
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
}

@-webkit-keyframes dot {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes dot {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 784px) {
  .App {
    padding: 0px 16px 0px 16px;

    h1 {
      margin: 0 0 0 0;
    }
  }
}
