.MusicPlayer {
  display: flex;
  align-items: center;

  .author {
    background-color: rgba(173, 216, 230, 0.24);
  }
  .author:hover {
    text-decoration: underline;
    cursor: pointer;
  }

  .audio-name {
    opacity: 0;
  }
  .fade-in {
    opacity: 1;
    transition: 500ms;
  }

  .fade-out {
    opacity: 0;
    transition: 500ms;
  }

  p {
    font-size: 0.8rem;
    color: white;
    font-family: CascadiaCode;
    letter-spacing: 2px;
  }

  .icon-button {
    width: 60px;
    height: 60px;
    // margin: 6px;

    .icon {
      color: rgb(137, 188, 255);
      //   color: orange;
    }
  }
}
